.cover-input, .NewPodcast__image {
    height: 72px;
    width: 72px;
    border-radius: 10px;

    display: flex;
    align-items: center;
    justify-content: center;
}

.cover-input {
    border: 0.5px solid rgba(0, 0, 0, 0.12);
    background: #F2F3F5;
}

.NewPodcast .Panel__in {
    display: flex;
    flex-direction: column;
}


.NewPodcast__image {
    margin-left: 12px;
    margin-right: 12px;
}


.cover-input__icon {
    color: #3F8AE0;
}

.NewPodcast__name {
}

.NewPodcast__TopForm {
    padding: 0;
    flex-grow: 1;
}

.NewPodcast__TopForm .FormLayout__row {
    padding: 0;
}

.NewPodcast__imageOverlay {
    padding: 0;
}

.NewPodcast__checkboxes .FormLayout__row {
    padding-bottom: 4px;
    padding-top: 4px;
}

.NewPodcast__checkbox {
    cursor: pointer;
}

.NewPodcast__placeholder .Placeholder__in {
    padding-top: 26px;
    padding-bottom: 18px;
}

.NewPodcast__privacyCell {
    margin-top: 10px;
}

.NewPodcast__privacyCell .Cell__description {
    margin-top: 4px;
}

.NewPodcast__audioInfo .Cell__aside {
    font-size: 13px;
    color: #99A2AD;
}

.NewPodcast__audioInfo .Cell__in {
    padding: 0;
}

.NewPodcast__audioInfo .Cell__children {
    margin-left: 12px;
}
